<h1 id="scaling-web-applications" tabindex="-1"><a class="header-anchor" href="#scaling-web-applications" aria-hidden="true">#</a> Scaling Web Applications</h1>
<p>Even if you optimize the code in your web application as much as possible and have it up and running on the best server available in the world, a day may come when it receives more requests than it has time to handle. That's when you need to scale your web application, and have it up and running on multiple servers to be able to handle the load. Let's take a closer look at an architecture we can use to achieve this and the challenges that comes with it.</p>
